.bg-image-swiper {
  height: 733px;
  background-image: url('../images/bg_share.jpg');
  background-position: center center;
  background-size: cover;
}

// swiper-wrapper 
.height-429 {
  height: 429px;
}

// 
.swipe-img {
  position: relative;
  width: 60%;
  height: 90%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 24px;

    &~.img-border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 10px;
      left: -10px;
      z-index: -1;
      border: 1px solid #7C7E77;
      border-radius: 24px;
    }
  }
}

//  swiper  border hidden
.overflow-visible {
  overflow: visible;
}

// 
.swipe-card-text {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #fff;
  width: 45%;
  border-radius: 24px 24px 24px 0;

  &-img {
    position: absolute;
    top: -50px;
    right: 32px;
    max-width: 100px;
    border-radius: 50%;
  }
}

// 
.swiper-buttons{
  position: absolute;
  bottom: 0;
  left: 30%;
  display: flex;
  transform: translate(-50%);
}
// 
.swiper-button-prev{
  position: unset;
  width: 24px;
  height: 24px;
  margin-top: 0;
}
.swiper-button-prev::after {
  content: ""; //
  font-size: 0; // icon 
}
