//  
.swiper-buttons{
  .swiper-pagination-custom {
    position: unset;
    width: auto;
  }
}
.swiper-pagination-bullet {
  background-color: #62DB54;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  width: 16px;
  border-radius: 8px;
  transition: width .2s ease;
}

// 
.text-green {
  color: #62DB54;
}

// 
.icon-size {
  width: 24px;
  height: 24px;
}

//  swiper
.swiper-share{
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}